<template>
  <div id="myroot">

    <div class="bg" style=""></div>
    <van-nav-bar title="我的足迹" fixed>
      <van-icon name="arrow-left" slot="left" color="black" @click="$router.go(-1)"/>
      <div slot="right" @click="cleraRoot(),deletes()">{{actionTitle}}</div>
    </van-nav-bar>
    <div id="container">
      <!--<van-list v-model="loading">-->
        <van-row>
          <van-col span="8" v-for="(item,index) in datasList" :key="index">
            <div class="box">
              <img style="width: 6.6rem;height: 6.6rem;" :src="item.thumb" alt="">
              <div class="price">
                <span>￥{{item.coupon_price}}</span>
              </div>
              <div class="radio">
                <van-checkbox v-model="radio" v-if="checkShow" checked-color="#ff5555"></van-checkbox>
              </div>
            </div>
          </van-col>
        </van-row>
      <!--</van-list>-->
    </div>
    <van-submit-bar button-text="删除" v-if="checkShow">
      <van-checkbox v-model="checked" style="margin-left:10px" checked-color="#ff5555">全选</van-checkbox>
    </van-submit-bar>
  </div>
</template>

<script>
  export default {
    name: "myroot",
    data() {
      return {
        datasList:[],
        checked: true,
        radio: '',
        loading: true,
        actionTitle:'清空',
        checkShow: false,

      }
    },
    mounted() {
      this.selectFootprint();
    },
    methods: {
      // onClickRight() {
      //   // this.$toast('完成');
      //   this.checkShow=!this.checkShow;
      //   this.actionTitle=this.checkShow===false?'编辑':'完成';
      //   this.action=this.checkShow===false?false:true;
      // },
      selectFootprint() {
        let self = this;
        this.$POST('/user/zuji', true).then(res => {
          console.log(res);
          if (res.code === 200) {
            self.datasList = res.data
          }
        }).catch(res => {
          this.$toast('连接服务器失败!');
          console.log(res);
        })
      },
      cleraRoot(){
        this.$POST('/user/zujidel', true).then(res => {
          console.log(res);
          if (res.code === 200) {
            this.$toast('已清空历史足迹')
          }
        }).catch(res => {
          this.$toast('连接服务器失败!');
          console.log(res);
        })
      },
      deletes(){
        var self = this
        self.datasList = []
      },
    },

  }
</script>

<style scoped>
  #myroot {
    font-size: 1rem;
    color: #000000;
    font-family: PingFang-SC-Medium;
    /*background-color: #ededed;*/
  }

  #container {
    padding: 0.5rem;
    margin-top: 40px;
    margin-bottom: 40px;
  }

  .box {
    margin: 0.4rem 0.2rem;
    position: relative;
  }

  .box > img {
    width: 100%;
    height: 100%;
    border-radius: 5px
  }

  .box > .price {
    text-align: left;
    color: #fe0000;;
  }

  .box > .radio {
    position: absolute;
    left: 0.4rem;
    top: 0.4rem;
  }
</style>



